.list_jobs {  }
.list_jobs .container { padding: 0px 15px; }
.list_jobs .table-responsive { overflow-y: visible; overflow-x: visible; margin-bottom: 20px; }
.list_jobs table { width: 100%; margin-top: 30px; }
.list_jobs table thead th,
.list_jobs table tbody td { white-space: normal; padding: 5px 10px; text-align: center; border: 1px solid #ddd; vertical-align: middle;}
.list_jobs table thead th { color:#fff !important; font-size: 18px; line-height: 34px;font-weight: 600; background:#1b2e42; }
.list_jobs table tbody td { line-height: 20px; padding: 10px 10px; }
.list_jobs table tbody tr:nth-child(even) td { background:#f1f1f1; }
.list_jobs table tbody td div i { display: inline-block; color:#333 !important; line-height: 15px; width: auto !important; padding: 5px; position: relative !important; margin: 0px !important; }
.list_jobs table tbody td div i .container123 { position: relative !important; top: -3px; margin: 0px !important; }
.list_jobs table tbody td div i .container123 ul li a { display: block;}
.list_jobs table tbody td .text-primary { color:#30303c !important; }
.list_jobs table tbody td div i .container123 .dropdown-menu { width: 150px; left:-70px; top: 32px; min-width: 0px; }


.single_job .container { padding: 0px 15px; }
.single_job .preview_table { margin: 0 auto; width: 500px; position: relative; }
.single_job .profile-img { position: absolute; left: -160px; top: 0px; display: inline-block; }
.single_job .table { display: table; margin: 0 auto; border-collapse: collapse;}
.single_job .table_r { display: table-row; }
.single_job .table_c { display: table-cell; width: 50%; padding: 10px 10px; vertical-align: middle; border: 1px solid #ddd; }
.single_job .table_c:first-child label { font-weight: 600; }
.single_job .table_c label { -webkit-transform: none; transform: none; min-width: 138px; margin-bottom: 0px; line-height: 20px !important; display: inline-block; float:left; width: 100%; }
.single_job .table_c p { margin: 0px !important; text-align:left; line-height: 20px !important; font-size: 0.72rem !important; width: 100%; display: inline-block; white-space: normal;}
.single_job .table_c p {  }
.single_job .table_c p b { color:#333 !important; }
.single_job .table_c p td { padding: 0px !important; }
.single_job .table_c .row { margin: 0px; }
.single_job .table_r:nth-child(odd) .table_c { background:#f1f1f1; }
.single_job .buttons { width: 100%; overflow: hidden; text-align: center; margin-top: 10px; }
.single_job .buttons a { display: inline-block; margin-top: 20px; }
.single_job .buttons a button { padding: 10px 20px; }
.single_job .buttons button { padding: 10px 10px; margin: 10px 10px 0px 10px !important; display: inline-block; float:none !important; margin-top: 20px; }
.single_job .table_r.last .table_c { background:#090328; }
.single_job .table_r.last .table_c label { color:#fff; font-size: 18px; font-weight: 600; }
.single_job .table_r.last .table_c p { color:#fff; font-size: 18px !important; font-weight: 600; }


@media (max-width: 991px) {
  .single_job .preview_table { width: 100%; float:right; }
  .single_job .profile-img { width: 20%; left: 0px; }
  .single_job .table { width: 80%; float:right; }
}

@media (max-width: 767px) {
  .list_jobs button#more-btn { min-width: 28px; }
  .list_jobs .table-responsive { overflow-x:auto; }
  .list_jobs table thead th { line-height: 20px !important;}
  .list_jobs table thead th, .vendor_ser_det table tbody td { font-size: 14px !important; padding: 5px 5px;  }

  .single_job .preview_table { margin-bottom: 40px; }
  .single_job .profile-img { width: 100%; position: relative; text-align: center; }
  .single_job .profile-img > img { display: inline-block; }
  .single_job .table { width: 100%; display: block; }
  .single_job .table_r { display: block; width: 100%; }
  .single_job .table_c { display: block; width: 100%; overflow: hidden;}
}

