.overlay {
  height: 0%;
  width: 40%;
  position: fixed;
  z-index: 10000000;
  top: 0;
  margin-top:150px;
  left: 0;
  margin-left:400px;
  /*background-color: rgb(0,0,0);
  background-color: rgba(0,0,0, 0.9);*/
  background-color:white;
  overflow-y: hidden;
  overflow-x  : hidden;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.overlay-content1 {
  position: relative;
  /* top: 25%; */
  width: 100%;
  text-align: center;
  margin-top: 30px;
}

.overlay a {
  padding: 8px;
  text-decoration: none;
  font-size: 36px;
  color: #818181;
  display: block;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.overlay a:hover, .overlay a:focus {
  color: #f1f1f1;
}

.overlay .closebtn {
  position: absolute;
  top: -10px;
  right:5px;
  font-size: 40px;
  font-family: inherit;
}

@media screen and (max-height: 450px) {
  .overlay {overflow-y: auto;overflow-x: auto;}
  .overlay a {font-size: 20px}
